   box-sizing: inherit;
 }
 /* Open Indicator States */
.v-select.open .open-indicator:before {
   -webkit-transform: rotate(315deg);
           transform: rotate(315deg);
}
.v-select.loading .open-indicator {
   opacity: 0;
}
.v-select.open .open-indicator {
   bottom: 1px;
}
 /* Dropdown Toggle */
.v-select .dropdown-toggle {
   -webkit-appearance: none;
   -moz-appearance: none;
   appearance: none;
   display: block;
   padding: 0;
   background-color: #fff;
   border: 1px solid rgba(60, 60, 60, .26);
   border-radius: 4px;
   white-space: normal;
   -webkit-transition: border-radius .25s;
   transition: border-radius .25s;
   width: 100%;
   visibility: visible;
   height: 20px!important;
}
.dropdown-toggle:hover, .dropdown-toggle:focus, .dropdown-toggle:active {
   background-color: #fff;
   border-color: #4b77be;
}
 /*
 .v-select .dropdown-toggle {
   min-width: 160px;
   margin-top: 3px;
   visibility: visible;
   opacity: 1;
   filter: none;
   border-radius: 4px;
   font-size: 13px;
   position: absolute;
   z-index: 9999;
   top: 100%;
   transition: none;
 }*/
.v-select .dropdown-toggle:after {
   visibility: hidden;
   display: block;
   font-size: 0;
   content: " ";
   clear: both;
   height: 0;
}
 /* Dropdown Toggle States */
.v-select.searchable .dropdown-toggle {
   cursor: text;
}
.v-select.unsearchable .dropdown-toggle {
   cursor: pointer;
}
.v-select.open .dropdown-toggle {
   border-bottom-color: transparent;
   border-bottom-left-radius: 0;
   border-bottom-right-radius: 0;
}
 /* Dropdown Menu */
 /*.v-select .dropdown-menu {
   display:block;
   position: absolute;
   top: 100%;
   left: 0;
   z-index: 1000;
   min-width: 160px;
   padding: 5px 0;
   margin: 0;
   width: 100%;
   overflow-y: scroll;
   border: 1px solid rgba(0, 0, 0, .26);
   box-shadow: 0px 3px 6px 0px rgba(0,0,0,.15);
   border-top: none;
   border-radius: 0 0 4px 4px;
   text-align: left;
   list-style: none;
   background: #fff;
 }*/
.v-select .dropdown-menu {
   background-color: #BDC3C7 !important;
   min-width: 100%;
   min-height: 100%;
   border: none;

   padding: 0;
   font-size: 13px;
   border-radius: 4px;
   box-shadow: none;
   width: auto;
   margin: 4px 0;
}
.v-select .no-options {
   text-align: center;
}
 /* Selected Tags */
.v-select .selected-tag {
   color: #333;
   /*background-color: #f0f0f0;
   border: 1px solid #ccc;
   border-radius: 4px;*/
   height: 22px;
   padding: 0px 10px;
   float: left;
   line-height: 22px;
}
.v-select.single .selected-tag {
   background-color: transparent;
   border-color: transparent;
}
.v-select.single.open .selected-tag {
   position: absolute;
   /*opacity: .5;*/
}
.v-select.single.open.searching .selected-tag,
 .v-select.single.loading .selected-tag {
   display: none;
}
.v-select .selected-tag .close {
   float: none;
   margin-right: 0;
   font-size: 20px;
   -webkit-appearance: none;
      -moz-appearance: none;
           appearance: none;
   padding: 0;
   cursor: pointer;
   background: 0 0;
   border: 0;
   font-weight: 700;
   line-height: 1;
   color: #000;
   text-shadow: 0 1px 0 #fff;
   filter: alpha(opacity=20);
   opacity: .2;
}
.v-select.single.searching:not(.open):not(.loading) input[type="search"] {
   opacity: .2;
}
 /* Search Input */
.v-select input[type="search"]::-webkit-search-decoration,
 .v-select input[type="search"]::-webkit-search-cancel-button,
 .v-select input[type="search"]::-webkit-search-results-button,
 .v-select input[type="search"]::-webkit-search-results-decoration {
   display: none;
}
.v-select input[type="search"]::-ms-clear {
   display: none;
}
.v-select input[type="search"],
 .v-select input[type="search"]:focus {
   appearance: none;
   -webkit-appearance: none;
   -moz-appearance: none;
   line-height: 1.42857143;
   font-size:1em;
   height: 34px;
   display: inline-block;
   border: none;
   outline: none;
   margin: 0;
   padding: 0 .5em;
   width: 10em;
   max-width: 100%;
   background: none;
   position: relative;
   box-shadow: none;
   float: left;
   clear: none;
}
 /* Search Input States */
.v-select.unsearchable input[type="search"] {
   max-width: 1px;
}
 /* List Items */
.v-select li {
   line-height: 1.42857143; /* Normalize line height */
   margin: 2px;
}
 /*.v-select li > a {
   display: block;
   padding: 3px 20px;
   clear: both;
   color: #fff; /* Overrides most CSS frameworks *
   white-space: nowrap;
 }
 */
.v-select li > a {
   line-height: 1.426;
   padding: 2px 10px;
   -webkit-user-select: none;
   -moz-user-select: none;
   -ms-user-select: none;
   user-select: none;
 /* transition: background-color .25s,color .25s; */
   white-space: nowrap;
   margin: 4px 0px;
   border-radius: 4px;
   color: #fff;
}
.v-select li:hover {
   cursor: pointer;
}
.v-select li:last-child > a  {
   border-top-right-radius: 4px;
   border-top-left-radius: 4px;
}
.v-select li:first-child > a  {
   border-bottom-right-radius: 4px;
   border-bottom-left-radius: 4px;
}
.v-select .dropdown-menu .active > a {
   color: #fff;
   background: none;
}
.v-select .dropdown-menu > .highlight > a {
   /*
    * required to override bootstrap 3's
    * .dropdown-menu > li > a:hover {} styles
    */
   background: #4b77be;
   color: #fff;
}
.v-select .highlight:not(:last-child) {
   margin-bottom: 0; /* Fixes Bulma Margin */
}
 /* Loading Spinner */
.v-select .spinner {
   opacity: 0;
   position: absolute;
   top: 5px;
   right: 10px;
   font-size: 5px;
   text-indent: -9999em;
   overflow: hidden;
   border-top: .9em solid rgba(100, 100, 100, .1);
   border-right: .9em solid rgba(100, 100, 100, .1);
   border-bottom: .9em solid rgba(100, 100, 100, .1);
   border-left: .9em solid rgba(60, 60, 60, .45);
   -webkit-transform: translateZ(0);
           transform: translateZ(0);
   -webkit-animation: vSelectSpinner 1.1s infinite linear;
           animation: vSelectSpinner 1.1s infinite linear;
   -webkit-transition: opacity .1s;
   transition: opacity .1s;
}
.v-select .spinner,
 .v-select .spinner:after {
   border-radius: 50%;
   width: 5em;
   height: 5em;
}
 /* Loading Spinner States */
.v-select.loading .spinner {
   opacity: 1;
}
 /* KeyFrames */
@-webkit-keyframes vSelectSpinner {
0% {
     -webkit-transform: rotate(0deg);
             transform: rotate(0deg);
}
100% {
     -webkit-transform: rotate(360deg);
             transform: rotate(360deg);
}
}
@keyframes vSelectSpinner {
0% {
     -webkit-transform: rotate(0deg);
             transform: rotate(0deg);
}
100% {
     -webkit-transform: rotate(360deg);
             transform: rotate(360deg);
}
}
 /* Dropdown Default Transition */
.fade-enter-active,
 .fade-leave-active {
   -webkit-transition: opacity .15s cubic-bezier(1.0, 0.5, 0.8, 1.0);
   transition: opacity .15s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fade-enter,
 .fade-leave-to {
   opacity: 0;
}



